body {
   padding: 0;
   font-family: 'Poppins', sans-serif;
}

/* BASE STYLING */
body {
   background-color: #f0f0ff;
   margin: 0;
   padding: 0;
   height: auto;
   min-height: 100vh;
   display: flex;
   justify-content: center;
   align-items: center;
}

:root {
   --border-radius: 0.75rem;
   --button-size: 3rem;
}

/* wrapper */
.todo-wrapper {
   text-align: center;
   border: 1px solid white;
   width: 500px;
   height: auto;
   background: #fafafa;
   border-radius: 0.75rem;
   border-radius: var(--border-radius);
   box-shadow: 0 20px 30px 0 rgba(1, 1, 1, 0.07);
   margin: 40px auto;
}

.ToDo-Header {
   color: black;
   font-weight: 400;
   text-transform: uppercase;
   margin: 70px auto 30px;
}

/* input and add button */
input {
   box-sizing: border-box;
   font-size: 1rem;
   background: transparent;
   width: calc(100% - 3rem - 5px);
   width: calc(100% - var(--button-size) - 5px);
   border: none;
   font-family: 'Poppins', sans-serif;
}

input:focus {
   outline: 0;
   border-color: white;
   color: #0d0d0d;
}

.input-wrapper {
   display: flex;
   align-items: center;
   justify-content: center;
   width: 100%;
   box-sizing: border-box;
   padding: 1.5rem;
   align-self: flex-start;
   box-shadow: 0 -10px 10px 0 rgba(1, 1, 1, 0.05);
}

.add-button {
   transition: all 0.3s ease-in-out;
   width: 3rem;
   width: var(--button-size);
   height: 3rem;
   height: var(--button-size);
   background: #3b44fb;
   color: white;
   text-align: center;
   font-size: 2rem;
   font-weight: 300;
   border-radius: 0.75rem;
   border-radius: var(--border-radius);
   border: 0;
   outline: 0;
   cursor: pointer;
}

.ToDo-Container {
   width: 80%;
   padding: 1.5rem 2rem;
   margin: 0 auto;
}

/* Animations */
@-webkit-keyframes fadeIn {
   0% {
      opacity: 0;
      margin-top: -50px;
   }
   50% {
      opacity: 0;
   }
   100% {
      opacity: 1;
      margin-top: 0px;
   }
}
@keyframes fadeIn {
   0% {
      opacity: 0;
      margin-top: -50px;
   }
   50% {
      opacity: 0;
   }
   100% {
      opacity: 1;
      margin-top: 0px;
   }
}

@-webkit-keyframes fadeOut {
   0% {
      opacity: 1;
      margin-top: 0px;
   }
   50% {
      opacity: 0;
   }
   100% {
      opacity: 0;
      margin-top: -50px;
   }
}

@keyframes fadeOut {
   0% {
      opacity: 1;
      margin-top: 0px;
   }
   50% {
      opacity: 0;
   }
   100% {
      opacity: 0;
      margin-top: -50px;
   }
}

:root {
   --border-radius: 0.75rem;
   --button-size: 3rem;
}

.tasks {
   display: flex;
   justify-content: center;
   align-items: center;
   box-shadow: 0 20px 30px 0 rgba(1, 1, 1, 0.07);
   border-radius: 0.75rem;
   border-radius: var(--border-radius);
   background-color: white;
   font-size: 1rem;
   font-weight: 600;
   letter-spacing: 0.03rem;
   -webkit-animation: fadeIn 0.5s linear;
           animation: fadeIn 0.5s linear;
   margin-bottom: 5px;
   position: relative;
   border-right: 0px solid lightgray;
   letter-spacing: -0.015rem;
   will-change: transform;
   transition: all 0.3s ease-in-out;
   animation: fadeIn 0.5s linear;
   overflow: hidden;
}

.tasks:hover > .ToDoItem-Delete {
   background: #f74a4a;
   -webkit-transform: translateX(0px);
           transform: translateX(0px);
}

.ToDoItem-Text {
   width: 90%;
   text-align: left;
   padding-left: 20px;
}

.ToDoItem-Delete {
   width: 60px;
   height: 60px;
   -webkit-transform: translateX(120px);
           transform: translateX(120px);
   /*padding: 5px;*/
   cursor: pointer;
   border-bottom-right-radius: 10px;
   border-top-right-radius: 10px;
   color: white;
   font-size: 18px;
   transition: all 0.3s ease-in-out;
}

.ToDoItem-Delete:hover {
   box-shadow: none;
}

